import { useDocumentsCollectionInfo } from "@rp/react/tanstack-api/documents"
import { LoadingSpinner } from "@rp/react/web/common/base-components"
import { Box } from "@rp/react/web/mui/core"
import { DocumentsCollectionViewer } from "../DocumentsCollectionViewer"
import { useDocumentsViewerData } from "../hooks"
import { useDocumentsCollectionParameters } from "../hooks/useDocumentsCollectionParameters"

type DocumentsCollectionContainer = {
  documentId?: string
  collectionId?: string
  isFromChat?: boolean
}

const DocumentsCollectionContainer = ({
  documentId: documentIdFromProps,
  collectionId: collectionIdFromProps,
  isFromChat = false,
}: DocumentsCollectionContainer) => {
  const { documentId, collectionId } = useDocumentsCollectionParameters({
    documentIdFromProps,
    collectionIdFromProps,
  })

  const { data: collectionDocuments, isLoading: isLoadingDocumentsCollection } =
    useDocumentsCollectionInfo({ collectionId: collectionId })

  const { audioItem, visualItems, eventInfo } = useDocumentsViewerData({
    documentId,
    collectionDocuments,
  })

  if (!collectionId) return null

  if (isLoadingDocumentsCollection) {
    return <LoadingSpinner />
  }

  return (
    <Box sx={{ display: "flex", width: "100%" }}>
      <DocumentsCollectionViewer
        audioItem={audioItem}
        visualItems={visualItems}
        eventInfo={eventInfo}
        documentId={documentId}
        isFromChat={isFromChat}
      />
    </Box>
  )
}

export { DocumentsCollectionContainer }
